<template>
  <div>
    <ToolBar>
      <div>
        <el-input
          placeholder="用户名"
          size="small"
          style="width: 140px"
          v-model="searchParams.username"
          clearable
        ></el-input>
        <el-input
          placeholder="订单号"
          size="small"
          style="width: 280px"
          v-model="searchParams.userOrderId"
          clearable
        ></el-input>
        <el-select
          v-model="searchParams.userCapitalType"
          clearable
          style="width: 140px"
          placeholder="类型"
          size="small"
        >
          <el-option v-for="(k,v,index) in userCapitalTypeEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-select
          v-model="searchParams.mode"
          clearable
          style="width: 140px"
          placeholder="方向"
          size="small"
        >
          <el-option v-for="(k,v,index) in outInEnum" :key="index" :label="k" :value="v"></el-option>
        </el-select>
        <el-date-picker
          v-model="searchParams.dateRange"
          size="small"
          type="datetimerange"
          :picker-options="pickerOptions"
          range-separator="至"
          start-placeholder="开始日期"
          end-placeholder="结束日期"
          align="right"
        ></el-date-picker>
        <el-button type="success" size="small" @click="refresh()">查询</el-button>
        <el-button type="warning" size="small" @click="clearSearchParams()">重置</el-button>
      </div>
    </ToolBar>
    <el-table
      :data="tableData"
      border
      style="width: 100%"
      :row-style="{height:'8px'}"
      :cell-style="{padding:'5px 0px'}"
    >
      <el-table-column type="expand">
        <template slot-scope="props">
          <el-form label-position="left" class="yb-table-expand">
            <el-form-item label="修改人">
              <span>{{ props.row.updateBy }}</span>
            </el-form-item>
            <el-form-item label="修改人时间">
              <span>{{ props.row.updateTime }}</span>
            </el-form-item>
          </el-form>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="username" label="用户名" width="200"></el-table-column>
      <el-table-column align="center" prop="userOrderId" label="订单号" width="280"></el-table-column>
      <el-table-column align="center" prop="mode" label="方向" :formatter="modeFormatter" width="140"></el-table-column>
      <el-table-column
        align="center"
        prop="userCapitalType"
        label="交易类型"
        :formatter="userCapitalTypeFormatter"
        width="140"
      ></el-table-column>
      <el-table-column align="center" prop="userBeforeAmount" label="交易前金额" width="100"></el-table-column>
      <el-table-column align="center" prop="userCapitalAmount" label="交易金额" width="100"></el-table-column>
      <el-table-column align="center" prop="userAfterAmount" label="交易后金额" width="100"></el-table-column>
      <el-table-column align="center" prop="createTime" label="创建时间" width="160"></el-table-column>
      <el-table-column align="center" prop="createBy" label="创建人" width="120"></el-table-column>
    </el-table>
    <Pagination
      :params="searchParams"
      :requestFunc="getCashFlowList"
      ref="pagination"
      @returnData="returnData"
    />
  </div>
</template>

<script>
import { getCashFlowList } from "@/api/cash/cashManage";
import { getDateList } from "@/utils/date";
import { resetObject } from "@/utils/common";
import { OutInEnum, UserCapitalTypeEnum } from "@/utils/Immutable";
export default {
  data() {
    return {
      searchParams: {
        username: "",
        userOrderId: "",
        dateRange: [],
        userCapitalType: ""
      },
      userCapitalTypeEnum: orderTypeEnum,
      pickerOptions: getDateList(),
      getCashFlowList: getCashFlowList,
      outInEnum: OutInEnum,
      tableData: []
    };
  },
  created() {},
  methods: {
    userCapitalTypeFormatter(a, b, v) {
      return UserCapitalTypeEnum[v];
    },
    modeFormatter(a, b, v) {
      return OutInEnum[v];
    },
    clearSearchParams() {
      resetObject(this.searchParams);
      this.refresh();
    },
    returnData(data) {
      this.tableData = data;
    },
    refresh() {
      this.$refs.pagination.Refresh(); //分页刷新
      this.showEditDialog = false;
    }
  }
};
</script>
<style  scoped>
</style>