import request from "@/utils/request.js";

/**
 * 资金管理
 */
export function getCashFlowList(params) {
    return request({
        url: "/apiadmin/usercapitalflow/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });
}
export function getPaymentList(params) {
    return request({
        url: "/apiadmin/payment/findlist",
        method: "get",
        params: params //注意：如果是get请求请使用 params: params
    });

}
